import {createSelector} from '@reduxjs/toolkit';
import {getCompanies} from '../../companies/selectors/selectors';
import {isContains} from '../../../utils/utils';
import {OptionShow} from '../../../const';
import {getEmployees, getFilters} from './selectors';

export const getFilteredEmployees = createSelector(
	[getEmployees, getFilters, getCompanies],
	(employees, filters, companies) => {
		const filteredCompanies = companies
			?.filter((company) => !!(company.name.toLowerCase().indexOf(filters.companyName.toLowerCase()) + 1));
		const filteredCompanyIds = filteredCompanies?.map((company) => company.id);

		const filteredEmployees = employees
			?.filter((employee) => !!(employee.fullName.toLowerCase().indexOf(filters.employeeName.toLowerCase()) + 1))
			.filter((employee) => !filters.companyName || employee.companyIds.length)
			.filter((employee) => isContains<number>(employee.companyIds, filteredCompanyIds));

		switch (filters.show) {
			case OptionShow.Blocked:
				return filteredEmployees?.filter((employee) => employee.isBlocked);
			case OptionShow.Active:
				return filteredEmployees?.filter((employee) => !employee.isBlocked);

			default: return filteredEmployees;
		}
	}
);
