import {Company} from '../../types/company';
import {getElementsByIds, getSurnameWithInitials} from '../../utils/utils';
import {Employee} from '../../types/employee';
import VisuallyHidden from '../styled/visually-hidden';
import {ButtonVariant, Color} from '../../const';
import React, {MouseEvent, useState} from 'react';
import {
	addEmployee,
	blockCompany,
	removeEmployee,
	unlockCompany,
	updateCompany,
	removeCompany, resetCompany
} from '../../store/companies/companies';
import {useDispatch} from 'react-redux';
import {AppDispatch} from '../../store/store';
import CustomDialog from '../custom-dialog/custom-dialog';
import Button from '../styled/button';
import {addCompany, removeCompany as removeCompanyFromEmployee} from '../../store/employees/employees';
import {toast} from 'react-toastify';
import CustomTextField from '../custom-text-field/custom-text-field';
import {GreyWrapper} from '../styled/grey-wrapper';
import {ControlsFlexWrapper} from '../styled/controls-flex-wrapper';
import {TableList} from '../styled/table-list';
import {ActionButton} from '../styled/action-button';
import {AddButton} from '../styled/add-button';
import {ActionsWrapper} from '../styled/action-wrapper';
import CustomAutocomplete from '../custom-autocomplete/custom-autocomplete';
import {Option} from '../../types/option';

type TableRowCompaniesProps = {
	company: Company,
	index: number,
	employees: Employee[] | null | undefined
}

function TableRowCompanies({company, index, employees}: TableRowCompaniesProps): JSX.Element {
	const {id, isBlocked, innerNumber, name, employeeIds, isChecked} = company;
	const dispatch = useDispatch<AppDispatch>();
	const [isAcceptRemoveEmployeeOpen, setIsAcceptRemoveEmployeeOpen] = useState(false);
	const [currentEmployeeId, setCurrentEmployeeId] = useState<number | null>(null);
	const [isAddEmployeeOpen, setIsAddEmployeeOpen] = useState(false);
	const [employeeToAdd, setEmployeeToAdd] = useState<Option | null>(null);
	const [isEditCompanyOpen, setIsEditCompanyOpen] = useState(false);
	const [newCompanyName, setNewCompanyName] = useState(name);
	const [isAcceptBlockCompanyOpen, setIsAcceptBlockCompanyOpen] = useState(false);
	const [isAcceptRemoveCompanyOpen, setIsAcceptRemoveCompanyOpen] = useState(false);
	const allEmployeeIds = employees?.map((employee) => employee.id);
	const employeeIdsOutOfCompany = allEmployeeIds
		?.filter((id) => !employeeIds.includes(id))
		.concat(employeeIds.filter((id) => !allEmployeeIds.includes(id)));
	const employeeOptions = employeeIdsOutOfCompany?.map((id) => {
		return {
			value: String(id),
			label: {
				main: String(employees?.find((employee) => employee.id === id)?.fullName)
			}
		}
	});
	const companyEmployees = getElementsByIds<Employee>(employeeIds, employees);

	/*function checkboxChangeHandler() {
		dispatch(setIsChecked({id, isChecked: !isChecked}));
	}*/

	function removeEmployeeClickHandler(id: number | undefined) {
		if (id) {
			setIsAcceptRemoveEmployeeOpen(true);
			setCurrentEmployeeId(id);
		}
	}

	function acceptRemoveEmployeeClickHandler() {
		dispatch(removeEmployee({companyId: id, employeeId: currentEmployeeId}));
		dispatch(removeCompanyFromEmployee({companyId: id, employeeId: currentEmployeeId}));
		setIsAcceptRemoveEmployeeOpen(false);
		setCurrentEmployeeId(null);
		toast('Сотрудник успешно удалён из компании', {type: 'success'});
	}

	function addEmployeeClickHandler(evt: MouseEvent<HTMLButtonElement>) {
		evt.preventDefault();
		dispatch(addEmployee({companyId: id, employeeId: Number(employeeToAdd?.value)}));
		dispatch(addCompany({companyId: id, employeeId: Number(employeeToAdd?.value)}));
		setIsAddEmployeeOpen(false);
		setEmployeeToAdd(null);
		toast('Сотрудник успешно добавлен', {type: 'success'});
	}

	function editCompanyClickHandler(evt: MouseEvent<HTMLButtonElement>) {
		evt.preventDefault();
		dispatch(updateCompany({companyId: id, newName: newCompanyName}));
		setIsEditCompanyOpen(false);
		toast('Компания успешно отредактирована', {type: 'success'});
	}

	function blockCompanyClickHandler() {
		dispatch(blockCompany(id));
		setIsAcceptBlockCompanyOpen(false);
		toast(
			isChecked ? 'Выбранные компании успешно заблокированы' : 'Компания успешно заблокирована',
			{type: 'success'}
		);
	}

	function unlockCompanyClickHandler() {
		dispatch(unlockCompany(id));
		setIsAcceptBlockCompanyOpen(false);
		toast(
			isChecked ? 'Выбранные компании успешно разблокированы' : 'Компания успешно разблокирована',
			{type: 'success'}
		);
	}

	function removeCompanyClickHandler() {
		dispatch(resetCompany(id));
		dispatch(removeCompany(id));

		companyEmployees.forEach((employee) => {
			dispatch(removeCompanyFromEmployee({companyId: id, employeeId: employee?.id}));
		});

		setIsAcceptRemoveCompanyOpen(false);
		toast(
			isChecked ? 'Выбранные компании успешно удалены' : 'Компания успешно удалена',
			{type: 'success'}
		);
	}

	function renderEmployees(companyEmployees: (Employee | undefined)[]) {
		return companyEmployees.map((employee) => (
			<li key={employee?.id}>
				{getSurnameWithInitials(employee?.fullName)}

				<ActionButton
					type="button"
					title="Удалить"
					aria-label="Удалить"
					marginLeft="auto"
					color={Color.DarkText}
					onClick={() => removeEmployeeClickHandler(employee?.id)}
				>
					<svg width="10" height="10" viewBox="0 0 10 10">
						<use xlinkHref="/img/sprite.svg#cross"></use>
					</svg>
				</ActionButton>
			</li>
		));
	}

	return (
		<>
			<tr key={id} className={isBlocked ? 'blocked' : ''}>
				{/*<td className="checkbox">
					<CustomCheckbox
						ariaLabel="Выбрать компанию"
						checked={isChecked}
						onChange={checkboxChangeHandler}
					/>
				</td>*/}

				<td>{index}</td>
				<td>{innerNumber}</td>
				<td className="table-name">{name}</td>

				<td className="table-employees">
					{!!employeeIds.length &&
						<TableList>
							{renderEmployees(companyEmployees)}
						</TableList>
					}

					<AddButton type="button" title="Добавить" onClick={() => setIsAddEmployeeOpen(true)}>
						<VisuallyHidden>Добавить</VisuallyHidden>
					</AddButton>
				</td>

				<td className="table-actions">
					<ActionsWrapper>
						{/*<ActionButton type="button" title="Редактировать" onClick={() => setIsEditCompanyOpen(true)}>
							<svg width="15" height="15" viewBox="0 0 15 15">
								<use xlinkHref="/img/sprite.svg#gear"></use>
							</svg>
						</ActionButton>*/}

						<ActionButton
							type="button"
							title={company.isBlocked ? 'Разблокировать' : 'Заблокировать'}
							color={company.isBlocked ? Color.Orange : ''}
							onClick={() => setIsAcceptBlockCompanyOpen(true)}
						>
							<svg width="17" height="17" viewBox="0 0 17 17">
								<use xlinkHref="/img/sprite.svg#prohibit"></use>
							</svg>
						</ActionButton>

						<ActionButton type="button" title="Удалить" onClick={() => setIsAcceptRemoveCompanyOpen(true)}>
							<svg width="16" height="16" viewBox="0 0 22 22">
								<use xlinkHref="/img/sprite.svg#remove"></use>
							</svg>
						</ActionButton>
					</ActionsWrapper>
				</td>
			</tr>

			<CustomDialog
				open={isAcceptRemoveEmployeeOpen}
				onClose={() => setIsAcceptRemoveEmployeeOpen(false)}
				title="Вы хотите удалить сотрудника из компании?"
			>
				<Button
					type="button"
					variant={ButtonVariant.Main}
					onClick={acceptRemoveEmployeeClickHandler}
				>
					Удалить
				</Button>
			</CustomDialog>

			<CustomDialog
				open={isAddEmployeeOpen}
				onClose={() => setIsAddEmployeeOpen(false)}
				title="Добавить сотрудника"
			>
				<GreyWrapper as="form">
					{employeeOptions?.length ? (
						<>
							<CustomAutocomplete
								id="employees-to-add"
								label="Сотрудники"
								options={employeeOptions}
								width="400px"
								widthSm="100%"
								value={employeeToAdd}
								onChange={(evt, newValue) => setEmployeeToAdd(newValue)}
							/>

							<ControlsFlexWrapper>
								<Button
									type="submit"
									variant={ButtonVariant.Main}
									onClick={addEmployeeClickHandler}
									disabled={!employeeToAdd}
								>
									Добавить
								</Button>
							</ControlsFlexWrapper>
						</>
					) : (
						<p>Нет доступных сотрудников для добавления</p>
					)}
				</GreyWrapper>
			</CustomDialog>

			<CustomDialog
				open={isEditCompanyOpen}
				onClose={() => setIsEditCompanyOpen(false)}
				title="Редактирование компании"
			>
				<GreyWrapper as="form">
					<CustomTextField
						id="new-company-name"
						label="Название компании"
						width="400px"
						widthSm="100%"
						value={newCompanyName}
						onChange={(evt) => setNewCompanyName(evt.target.value)}
						autoFocus
					/>

					<ControlsFlexWrapper>
						<Button
							type="submit"
							variant={ButtonVariant.Main}
							onClick={editCompanyClickHandler}
							disabled={!newCompanyName}
						>
							Редактировать
						</Button>
					</ControlsFlexWrapper>
				</GreyWrapper>
			</CustomDialog>

			<CustomDialog
				open={isAcceptBlockCompanyOpen}
				onClose={() => setIsAcceptBlockCompanyOpen(false)}
				title={`Вы хотите ${isBlocked ? 'разблокировать' : 'заблокировать'} ${isChecked ? 'выбранные компании' : 'компанию'}?`}
			>
				<Button
					type="button"
					variant={ButtonVariant.Main}
					onClick={isBlocked ? unlockCompanyClickHandler : blockCompanyClickHandler}
				>
					{isBlocked ? 'Разблокировать' : 'Заблокировать'}
				</Button>
			</CustomDialog>

			<CustomDialog
				open={isAcceptRemoveCompanyOpen}
				onClose={() => setIsAcceptRemoveCompanyOpen(false)}
				title={`Вы хотите удалить ${isChecked ? 'выбранные компании' : 'компанию'}?`}
			>
				<Button
					type="button"
					variant={ButtonVariant.Main}
					onClick={removeCompanyClickHandler}
				>
					Удалить
				</Button>
			</CustomDialog>
		</>
	);
}

export default TableRowCompanies;
