import {Role} from '../const';
import {LoginInfo} from '../types/login-info';

export const users = [
	{
		id: 1,
		fullName: 'Колесников Денис Степанович',
		role: Role.Admin,
		email: 'admin@terminal.ru',
		password: 'qweqweqwe',
		token: 'admintoken'
	}
];

export function checkAccount({login, password}: LoginInfo) {
	const user = users.find((user) => login === user.email);

	if (user && user.password === password) {
		return {
			fullName: user.fullName,
			role: user.role,
			token: user.token
		}
	}

	return false;
}
