import {CompaniesState} from '../../types/state';
import {createSlice} from '@reduxjs/toolkit';
import {companies, handbookCompanies} from '../../mocks/companies';
import {OptionShow} from '../../const';

const initialState: CompaniesState = {
	companies: companies,
	handbookCompanies: handbookCompanies,
	filters: {
		innerNumber: '',
		companyName: '',
		employeeName: '',
		show: OptionShow.All
	}
}

const companiesSlice = createSlice({
	name: 'companies',
	initialState,
	reducers: {
		setIsChecked: (state, action) => {
			const {id, isChecked} = action.payload;
			const company = state.companies?.find((company) => company.id === id);

			if (company) {
				company.isChecked = isChecked;
			}
		},
		addCompany: (state, action) => {
			const company = state.handbookCompanies?.find((company) => company.id === action.payload);

			if (company) {
				state.companies?.push(company);
				state.handbookCompanies = state.handbookCompanies
					?.filter((handbookCompany) => handbookCompany.id !== company.id);
			}
		},
		removeEmployee: (state, action) => {
			const {companyId, employeeId} = action.payload;
			const company = state.companies?.slice().find((company) => company.id === companyId);

			if (company) {
				company.employeeIds = company.employeeIds.filter((id) => id !== employeeId);
			}
		},
		addEmployee: (state, action) => {
			const {companyId, employeeId} = action.payload;
			const company = state.companies?.slice().find((company) => company.id === companyId);

			if (company) {
				company.employeeIds.push(employeeId);
			}
		},
		updateCompany: (state, action) => {
			const {companyId, newName} = action.payload;
			const company = state.companies?.slice().find((company) => company.id === companyId);

			if (company) {
				company.name = newName;
			}
		},
		blockCompany: (state, action) => {
			const company = state.companies?.slice().find((company) => company.id === action.payload);

			if (company) {
				if (company.isChecked) {
					state.companies?.filter((company) => company.isChecked).forEach((company) => company.isBlocked = true);
				} else {
					company.isBlocked = true;
				}
			}
		},
		unlockCompany: (state, action) => {
			const company = state.companies?.slice().find((company) => company.id === action.payload);

			if (company) {
				if (company.isChecked) {
					state.companies?.filter((company) => company.isChecked).forEach((company) => company.isBlocked = false);
				} else {
					company.isBlocked = false;
				}
			}
		},
		removeCompany: (state, action) => {
			const company = state.companies?.find((company) => company.id === action.payload);

			if (company) {
				if (company.isChecked && state.companies) {
					state.handbookCompanies?.push(...state.companies.filter((companyItem) => companyItem.isChecked));

					state.companies
						.filter((companyItem) => companyItem.isChecked)
						.forEach((companyItem) => state.companies = state.companies
							?.filter((filteredCompany) => companyItem.id !== filteredCompany.id));
				} else {
					state.handbookCompanies?.push(company);
					state.companies = state.companies?.filter((companyItem) => companyItem.id !== company.id);
				}
			}
		},
		resetCompany: (state, action) => {
			const company = state.companies?.find((company) => company.id === action.payload);

			if (company) {
				company.employeeIds = [];
			}
		},
		setFilters: (state, action) => {
			state.filters = Object.assign(
				{},
				state.filters,
				action.payload
			);
		},
		clearFilters: (state) => {
			state.filters = initialState.filters;
		}
	}
});

export const {
	setIsChecked,
	addCompany,
	removeEmployee,
	addEmployee,
	updateCompany,
	blockCompany,
	unlockCompany,
	removeCompany,
	resetCompany,
	setFilters,
	clearFilters
} = companiesSlice.actions
export default companiesSlice.reducer;
