import {useDispatch, useSelector} from 'react-redux';
import React, {MouseEvent, useState} from 'react';
import {AppDispatch} from '../../store/store';
import {ButtonVariant, ControlButtonIcon, showOptions} from '../../const';
import {SelectChangeEvent} from '@mui/material';
import {addEmployee, clearFilters, setFilters} from '../../store/employees/employees';
import {toast} from 'react-toastify';
import VisuallyHidden from '../styled/visually-hidden';
import CustomTextField from '../custom-text-field/custom-text-field';
import CustomDialog from '../custom-dialog/custom-dialog';
import Button from '../styled/button';
import {getFilters, getHandbookEmployees} from '../../store/employees/selectors/selectors';
import {StyledScreenControls} from '../styled/styled-screen-controls';
import {ControlsForm} from '../styled/controls-form';
import {ControlsWrapper} from '../styled/controls-wrapper';
import {ButtonControl} from '../styled/button-control';
import {IconButton} from '../styled/icon-button';
import {GreyWrapper} from '../styled/grey-wrapper';
import {ControlsFlexWrapper} from '../styled/controls-flex-wrapper';
import CustomAutocomplete from '../custom-autocomplete/custom-autocomplete';
import {Option} from '../../types/option';
import {FilterForm} from '../styled/filter-form';

type ScreenControlsEmployeesProps = {
	closeDialog?: () => void
}

function ScreenControlsEmployees({closeDialog}: ScreenControlsEmployeesProps): JSX.Element {
	const filters = useSelector(getFilters);
	const [companyName, setCompanyName] = useState(filters.companyName);
	const [employee, setEmployee] = useState(filters.employeeName);
	const [isDialogOpen, setIsDialogOpen] = useState(false);
	const [employeeToAdd, setEmployeeToAdd] = useState<Option | null>(null);
	const dispatch = useDispatch<AppDispatch>();
	const handbookEmployees = useSelector(getHandbookEmployees);

	const employeeOptions = handbookEmployees?.map((employee) => {
		return {
			value: String(employee.id),
			label: {
				main: employee.fullName
			}
		}
	});

	function resetClickHandler(evt: MouseEvent<HTMLButtonElement>) {
		evt.preventDefault();

		setCompanyName('');
		setEmployee('');

		dispatch(clearFilters());

		if (closeDialog) {
			closeDialog();
		}
	}

	function viewChangeHandler(evt: SelectChangeEvent) {
		dispatch(setFilters({show: evt.target.value}));

		if (closeDialog) {
			closeDialog();
		}
	}

	function submitClickHandler(evt: MouseEvent<HTMLButtonElement>) {
		evt.preventDefault();

		dispatch(setFilters({
			companyName: companyName,
			employeeName: employee
		}));

		if (closeDialog) {
			closeDialog();
		}
	}

	function addEmployeeClickHandler(evt: MouseEvent<HTMLButtonElement>) {
		evt.preventDefault();
		dispatch(addEmployee(Number(employeeToAdd?.value)));
		setIsDialogOpen(false);
		setEmployeeToAdd(null);
		toast('Сотрудник успешно добавлен', {type: 'success'});

		if (closeDialog) {
			closeDialog();
		}
	}

	return (
		<StyledScreenControls>
			<VisuallyHidden as="h2">Управление</VisuallyHidden>

			<ControlsForm>
				<CustomTextField
					id="employee"
					label="Сотрудник"
					value={employee}
					width="175px"
					widthSm="100%"
					widthMd="130px"
					marginTopSm="8px"
					onChange={(evt) => {
						setEmployee(evt.target.value);
					}}
				/>

				<CustomTextField
					id="company-name"
					label="Название компании"
					value={companyName}
					width="175px"
					widthSm="100%"
					widthMd="130px"
					onChange={(evt) => {
						setCompanyName(evt.target.value);
					}}
				/>

				<IconButton type="submit" onClick={submitClickHandler} variant={ButtonVariant.Main}>
					<svg width="24" height="24" viewBox="0 0 24 24">
						<use xlinkHref="/img/sprite.svg#check-circle"></use>
					</svg>

					<span>Применить</span>
				</IconButton>

				<IconButton type="reset" onClick={resetClickHandler} variant={ButtonVariant.Secondary}>
					<svg width="24" height="24" viewBox="0 0 24 24">
						<use xlinkHref="/img/sprite.svg#cross-circle"></use>
					</svg>

					<span>Сбросить</span>
				</IconButton>
			</ControlsForm>

			<ControlsWrapper>
				<ButtonControl icon={ControlButtonIcon.Add} onClick={() => setIsDialogOpen(true)}>
					Добавить
				</ButtonControl>

				<ButtonControl icon={ControlButtonIcon.Excel} marginLeft="20px">Экспортировать в Excel</ButtonControl>

				<FilterForm>
					<CustomTextField
						id="view-option"
						ariaLabel="Отображение"
						select
						options={showOptions}
						value={filters.show}
						width="202px"
						widthSm="100%"
						onChange={viewChangeHandler}
					/>
				</FilterForm>
			</ControlsWrapper>

			<CustomDialog
				open={isDialogOpen}
				onClose={() => setIsDialogOpen(false)}
				title="Добавить сотрудника"
			>
				<GreyWrapper as="form">
					{employeeOptions?.length ? (
						<>
							<CustomAutocomplete
								id="employees-to-add"
								label="Сотрудник"
								options={employeeOptions}
								width="400px"
								widthSm="100%"
								value={employeeToAdd}
								onChange={(evt, newValue) => setEmployeeToAdd(newValue)}
							/>

							<ControlsFlexWrapper>
								<Button
									type="submit"
									variant={ButtonVariant.Main}
									onClick={addEmployeeClickHandler}
									disabled={!employeeToAdd}
								>
									Добавить
								</Button>
							</ControlsFlexWrapper>
						</>
					) : (
						<p>Нет доступных сотрудников для добавления</p>
					)}
				</GreyWrapper>
			</CustomDialog>
		</StyledScreenControls>
	);
}

export default ScreenControlsEmployees;
