import {State} from '../../../types/state';
import {ManagerFilters} from '../../../types/filters';
import {NameSpace} from '../../root-reducer';
import {Manager} from '../../../types/manager';

export const getFilters = (state: State): ManagerFilters => state[NameSpace.Managers].filters;
export const getHandbookManagers = (state: State): Manager[] | null | undefined => state[NameSpace.Managers].handbookManagers;
export const getManagers = (state: State): Manager[] | null | undefined => state[NameSpace.Managers].managers;
