import {EmployeesState} from '../../types/state';
import {employees, handbookEmployees} from '../../mocks/employees';
import {createSlice} from '@reduxjs/toolkit';
import {OptionShow} from '../../const';

const initialState: EmployeesState = {
	employees: employees,
	handbookEmployees: handbookEmployees,
	filters: {
		companyName: '',
		employeeName: '',
		show: OptionShow.All
	}
}

const employeesSlice = createSlice({
	name: 'employees',
	initialState,
	reducers: {
		removeCompany: (state, action) => {
			const {companyId, employeeId} = action.payload;
			const employee = state.employees?.find((employee) => employee.id === employeeId);

			if (employee) {
				employee.companyIds = employee.companyIds.filter((id) => id !== companyId);
			}
		},
		addCompany: (state, action) => {
			const {companyId, employeeId} = action.payload;
			const employee = state.employees?.slice().find((employee) => employee.id === employeeId);

			if (employee) {
				employee.companyIds.push(companyId);
			}
		},
		addEmployee: (state, action) => {
			const employee = state.handbookEmployees?.find((employee) => employee.id === action.payload);

			if (employee) {
				state.employees?.push(employee);
				state.handbookEmployees = state.handbookEmployees
					?.filter((handbookEmployee) => handbookEmployee.id !== employee.id);
			}
		},
		setIsChecked: (state, action) => {
			const {id, isChecked} = action.payload;
			const employee = state.employees?.find((employee) => employee.id === id);

			if (employee) {
				employee.isChecked = isChecked;
			}
		},
		updateEmployee: (state, action) => {
			const {employeeId, newFullName} = action.payload;
			const employee = state.employees?.slice().find((employee) => employee.id === employeeId);

			if (employee) {
				employee.fullName = newFullName;
			}
		},
		blockEmployee: (state, action) => {
			const employee = state.employees?.slice().find((employee) => employee.id === action.payload);

			if (employee) {
				if (employee.isChecked) {
					state.employees?.filter((employee) => employee.isChecked).forEach((employee) => employee.isBlocked = true);
				} else {
					employee.isBlocked = true;
				}
			}
		},
		unlockEmployee: (state, action) => {
			const employee = state.employees?.slice().find((employee) => employee.id === action.payload);

			if (employee) {
				if (employee.isChecked) {
					state.employees?.filter((employee) => employee.isChecked).forEach((employee) => employee.isBlocked = false);
				} else {
					employee.isBlocked = false;
				}
			}
		},
		removeEmployee: (state, action) => {
			const employee = state.employees?.find((employee) => employee.id === action.payload);

			if (employee) {
				if (employee.isChecked && state.employees) {
					state.handbookEmployees?.push(...state.employees.filter((employeeItem) => employeeItem.isChecked));

					state.employees
						.filter((employeeItem) => employeeItem.isChecked)
						.forEach((employeeItem) => state.employees = state.employees
							?.filter((filteredEmployee) => employeeItem.id !== filteredEmployee.id));
				} else {
					state.handbookEmployees?.push(employee);
					state.employees = state.employees?.filter((employeeItem) => employeeItem.id !== employee.id);
				}
			}
		},
		resetEmployee: (state, action) => {
			const employee = state.employees?.find((employee) => employee.id === action.payload);

			if (employee) {
				employee.companyIds = [];
			}
		},
		setFilters: (state, action) => {
			state.filters = Object.assign(
				{},
				state.filters,
				action.payload
			);
		},
		clearFilters: (state) => {
			state.filters = initialState.filters;
		}
	}
});

export const {
	removeCompany,
	addCompany,
	addEmployee,
	setIsChecked,
	updateEmployee,
	blockEmployee,
	unlockEmployee,
	removeEmployee,
	resetEmployee,
	setFilters,
	clearFilters
} = employeesSlice.actions;
export default employeesSlice.reducer;
