import {createSelector} from '@reduxjs/toolkit';
import {getCompanies} from '../../companies/selectors/selectors';
import {isContains} from '../../../utils/utils';
import {OptionShow} from '../../../const';
import {getFilters, getManagers} from './selectors';

export const getFilteredManagers = createSelector(
	[getManagers, getFilters, getCompanies],
	(managers, filters, companies) => {
		const filteredCompanies = companies
			?.filter((company) => !!(company.name.toLowerCase().indexOf(filters.companyName.toLowerCase()) + 1));
		const filteredCompanyIds = filteredCompanies?.map((company) => company.id);

		const filteredManagers = managers
			?.filter((manager) => !!(manager.fullName.toLowerCase().indexOf(filters.managerName.toLowerCase()) + 1))
			.filter((manager) => !filters.companyName || manager.companyIds.length)
			.filter((manager) => isContains<number>(manager.companyIds, filteredCompanyIds));

		switch (filters.show) {
			case OptionShow.Blocked:
				return filteredManagers?.filter((manager) => manager.isBlocked);
			case OptionShow.Active:
				return filteredManagers?.filter((manager) => !manager.isBlocked);

			default: return filteredManagers;
		}
	}
);
