import {ManagersState} from '../../types/state';
import {handbookManagers, managers} from '../../mocks/managers';
import {OptionShow} from '../../const';
import {createSlice} from '@reduxjs/toolkit';

const initialState: ManagersState = {
	managers: managers,
	handbookManagers: handbookManagers,
	filters: {
		companyName: '',
		managerName: '',
		show: OptionShow.All
	}
}

const managersSlice = createSlice({
	name: 'managers',
	initialState,
	reducers: {
		setFilters: (state, action) => {
			state.filters = Object.assign(
				{},
				state.filters,
				action.payload
			);
		},
		clearFilters: (state) => {
			state.filters = initialState.filters;
		},
		addManager: (state, action) => {
			const manager = state.handbookManagers?.find((manager) => manager.id === action.payload);

			if (manager) {
				state.managers?.push(manager);
				state.handbookManagers = state.handbookManagers
					?.filter((handbookManager) => handbookManager.id !== manager.id);
			}
		},
		setIsChecked: (state, action) => {
			const {id, isChecked} = action.payload;
			const manager = state.managers?.find((manager) => manager.id === id);

			if (manager) {
				manager.isChecked = isChecked;
			}
		},
		addCompany: (state, action) => {
			const {companyId, managerId} = action.payload;
			const manager = state.managers?.slice().find((manager) => manager.id === managerId);

			if (manager) {
				manager.companyIds.push(companyId);
			}
		},
		blockManager: (state, action) => {
			const manager = state.managers?.slice().find((manager) => manager.id === action.payload);

			if (manager) {
				if (manager.isChecked) {
					state.managers?.filter((manager) => manager.isChecked).forEach((manager) => manager.isBlocked = true);
				} else {
					manager.isBlocked = true;
				}
			}
		},
		unlockManager: (state, action) => {
			const manager = state.managers?.slice().find((manager) => manager.id === action.payload);

			if (manager) {
				if (manager.isChecked) {
					state.managers?.filter((manager) => manager.isChecked).forEach((manager) => manager.isBlocked = false);
				} else {
					manager.isBlocked = false;
				}
			}
		},
		removeManager: (state, action) => {
			const manager = state.managers?.find((manager) => manager.id === action.payload);

			if (manager) {
				if (manager.isChecked && state.managers) {
					state.handbookManagers?.push(...state.managers.filter((managerItem) => managerItem.isChecked));

					state.managers
						.filter((managerItem) => managerItem.isChecked)
						.forEach((managerItem) => state.managers = state.managers
							?.filter((filteredManager) => managerItem.id !== filteredManager.id));
				} else {
					state.handbookManagers?.push(manager);
					state.managers = state.managers?.filter((managerItem) => managerItem.id !== manager.id);
				}
			}
		},
		resetManager: (state, action) => {
			const manager = state.managers?.find((manager) => manager.id === action.payload);

			if (manager) {
				manager.companyIds = [];
			}
		}
	}
});

export const {
	setFilters,
	clearFilters,
	addManager,
	setIsChecked,
	addCompany,
	blockManager,
	unlockManager,
	removeManager,
	resetManager
} = managersSlice.actions;
export default managersSlice.reducer;
