import {State} from '../../../types/state';
import {NameSpace} from '../../root-reducer';
import {Employee} from '../../../types/employee';
import {EmployeeFilters} from '../../../types/filters';

export const getEmployees = (state: State): Employee[] | null | undefined => state[NameSpace.Employees].employees;
export const getHandbookEmployees = (state: State): Employee[] | null | undefined => state[NameSpace.Employees].handbookEmployees;
export const getFilters = (state: State): EmployeeFilters => state[NameSpace.Employees].filters;
