import {createSelector} from '@reduxjs/toolkit';
import {getEmployees} from '../../employees/selectors/selectors';
import {isContains} from '../../../utils/utils';
import {OptionShow} from '../../../const';
import {getCompanies, getFilters} from './selectors';

export const getFilteredCompanies = createSelector(
	[getCompanies, getFilters, getEmployees],
	(companies, filters, employees) => {
		const filteredEmployees = employees
			?.filter((employee) => !!(employee.fullName.toLowerCase().indexOf(filters.employeeName.toLowerCase()) + 1));
		const filteredEmployeeIds = filteredEmployees?.map((employee) => employee.id);

		const filteredCompanies = companies
			?.filter((company) => !!(company.innerNumber.toLowerCase().indexOf(filters.innerNumber.toLowerCase()) + 1))
			.filter((company) => !!(company.name.toLowerCase().indexOf(filters.companyName.toLowerCase()) + 1))
			.filter((company) => !filters.employeeName || company.employeeIds.length)
			.filter((company) => isContains<number>(company.employeeIds, filteredEmployeeIds));

		switch (filters.show) {
			case OptionShow.Blocked:
				return filteredCompanies?.filter((company) => company.isBlocked);
			case OptionShow.Active:
				return filteredCompanies?.filter((company) => !company.isBlocked);

			default: return filteredCompanies;
		}
	}
);
