import {StyledScreenControls} from '../styled/styled-screen-controls';
import VisuallyHidden from '../styled/visually-hidden';
import React, {MouseEvent, useState} from 'react';
import {ControlsForm} from '../styled/controls-form';
import {useDispatch, useSelector} from 'react-redux';
import {getFilters, getHandbookManagers} from '../../store/managers/selectors/selectors';
import CustomTextField from '../custom-text-field/custom-text-field';
import {IconButton} from '../styled/icon-button';
import {ButtonVariant, ControlButtonIcon, showOptions} from '../../const';
import {AppDispatch} from '../../store/store';
import {addManager, clearFilters, setFilters} from '../../store/managers/managers';
import {ButtonControl} from '../styled/button-control';
import {FilterForm} from '../styled/filter-form';
import {ControlsWrapper} from '../styled/controls-wrapper';
import {SelectChangeEvent} from '@mui/material';
import {GreyWrapper} from '../styled/grey-wrapper';
import CustomAutocomplete from '../custom-autocomplete/custom-autocomplete';
import {ControlsFlexWrapper} from '../styled/controls-flex-wrapper';
import Button from '../styled/button';
import CustomDialog from '../custom-dialog/custom-dialog';
import {Option} from '../../types/option';
import {toast} from 'react-toastify';

type ScreenControlsManagersProps = {
	closeDialog?: () => void
}

function ScreenControlsManagers({closeDialog}: ScreenControlsManagersProps): JSX.Element {
	const filters = useSelector(getFilters);
	const [manager, setManager] = useState(filters.managerName);
	const [companyName, setCompanyName] = useState(filters.companyName);
	const [isDialogOpen, setIsDialogOpen] = useState(false);
	const [managerToAdd, setManagerToAdd] = useState<Option | null>(null);
	const dispatch = useDispatch<AppDispatch>();
	const handbookManagers = useSelector(getHandbookManagers);

	const managerOptions = handbookManagers?.map((manager) => {
		return {
			value: String(manager.id),
			label: {
				main: manager.fullName
			}
		}
	});

	function submitClickHandler(evt: MouseEvent<HTMLButtonElement>) {
		evt.preventDefault();

		dispatch(setFilters({
			companyName: companyName,
			managerName: manager
		}));

		if (closeDialog) {
			closeDialog();
		}
	}

	function resetClickHandler(evt: MouseEvent<HTMLButtonElement>) {
		evt.preventDefault();

		setCompanyName('');
		setManager('');

		dispatch(clearFilters());

		if (closeDialog) {
			closeDialog();
		}
	}

	function viewChangeHandler(evt: SelectChangeEvent) {
		dispatch(setFilters({show: evt.target.value}));

		if (closeDialog) {
			closeDialog();
		}
	}

	function addManagerClickHandler(evt: MouseEvent<HTMLButtonElement>) {
		evt.preventDefault();
		dispatch(addManager(Number(managerToAdd?.value)));
		setIsDialogOpen(false);
		setManagerToAdd(null);
		toast('Менеджер успешно добавлен', {type: 'success'});

		if (closeDialog) {
			closeDialog();
		}
	}

	return (
		<StyledScreenControls>
			<VisuallyHidden as="h2">Управление</VisuallyHidden>

			<ControlsForm>
				<CustomTextField
					id="manager"
					label="Менеджер"
					value={manager}
					width="175px"
					widthSm="100%"
					widthMd="130px"
					marginTopSm="8px"
					onChange={(evt) => {
						setManager(evt.target.value);
					}}
				/>

				<CustomTextField
					id="company-name"
					label="Название компании"
					value={companyName}
					width="175px"
					widthSm="100%"
					widthMd="130px"
					onChange={(evt) => {
						setCompanyName(evt.target.value);
					}}
				/>

				<IconButton type="submit" onClick={submitClickHandler} variant={ButtonVariant.Main}>
					<svg width="24" height="24" viewBox="0 0 24 24">
						<use xlinkHref="/img/sprite.svg#check-circle"></use>
					</svg>

					<span>Применить</span>
				</IconButton>

				<IconButton type="reset" onClick={resetClickHandler} variant={ButtonVariant.Secondary}>
					<svg width="24" height="24" viewBox="0 0 24 24">
						<use xlinkHref="/img/sprite.svg#cross-circle"></use>
					</svg>

					<span>Сбросить</span>
				</IconButton>
			</ControlsForm>

			<ControlsWrapper>
				<ButtonControl icon={ControlButtonIcon.Add} onClick={() => setIsDialogOpen(true)}>
					Добавить
				</ButtonControl>

				<ButtonControl icon={ControlButtonIcon.Excel} marginLeft="20px">Экспортировать в Excel</ButtonControl>

				<FilterForm>
					<CustomTextField
						id="view-option"
						ariaLabel="Отображение"
						select
						options={showOptions}
						value={filters.show}
						width="202px"
						widthSm="100%"
						onChange={viewChangeHandler}
					/>
				</FilterForm>
			</ControlsWrapper>

			<CustomDialog
				open={isDialogOpen}
				onClose={() => setIsDialogOpen(false)}
				title="Добавить менеждера"
			>
				<GreyWrapper as="form">
					{managerOptions?.length ? (
						<>
							<CustomAutocomplete
								id="managers-to-add"
								label="Менеджер"
								options={managerOptions}
								width="400px"
								widthSm="100%"
								value={managerToAdd}
								onChange={(evt, newValue) => setManagerToAdd(newValue)}
							/>

							<ControlsFlexWrapper>
								<Button
									type="submit"
									variant={ButtonVariant.Main}
									onClick={addManagerClickHandler}
									disabled={!managerToAdd}
								>
									Добавить
								</Button>
							</ControlsFlexWrapper>
						</>
					) : (
						<p>Нет доступных менеджеров для добавления</p>
					)}
				</GreyWrapper>
			</CustomDialog>
		</StyledScreenControls>
	);
}

export default ScreenControlsManagers;
