import {State} from '../../../types/state';
import {Company} from '../../../types/company';
import {NameSpace} from '../../root-reducer';
import {CompanyFilters} from '../../../types/filters';

export const getCompanies = (state: State): Company[] | null | undefined => state[NameSpace.Companies].companies;
export const getHandbookCompanies = (state: State): Company[] | null | undefined => state[NameSpace.Companies].handbookCompanies;
export const getFilters = (state: State): CompanyFilters => state[NameSpace.Companies].filters;


