import React, {MouseEvent, useState} from 'react';
import VisuallyHidden from '../styled/visually-hidden';
import CustomTextField from '../custom-text-field/custom-text-field';
import {ButtonVariant, ControlButtonIcon, showOptions} from '../../const';
import {SelectChangeEvent} from '@mui/material';
import {useDispatch, useSelector} from 'react-redux';
import {AppDispatch} from '../../store/store';
import {getFilters, getHandbookCompanies} from '../../store/companies/selectors/selectors';
import CustomDialog from '../custom-dialog/custom-dialog';
import Button from '../styled/button';
import {addCompany, clearFilters, setFilters} from '../../store/companies/companies';
import {toast} from 'react-toastify';
import {StyledScreenControls} from '../styled/styled-screen-controls';
import {ControlsForm} from '../styled/controls-form';
import {IconButton} from '../styled/icon-button';
import {ControlsWrapper} from '../styled/controls-wrapper';
import {ButtonControl} from '../styled/button-control';
import {GreyWrapper} from '../styled/grey-wrapper';
import {ControlsFlexWrapper} from '../styled/controls-flex-wrapper';
import CustomAutocomplete from '../custom-autocomplete/custom-autocomplete';
import {Option} from '../../types/option';
import {FilterForm} from '../styled/filter-form';

type ScreenControlsCompaniesProps = {
	closeDialog?: () => void
}

function ScreenControlsCompanies({closeDialog}: ScreenControlsCompaniesProps): JSX.Element {
	const filters = useSelector(getFilters);
	const [innerNumber, setInnerNumber] = useState(filters.innerNumber);
	const [companyName, setCompanyName] = useState(filters.companyName);
	const [employee, setEmployee] = useState(filters.employeeName);
	const [isDialogOpen, setIsDialogOpen] = useState(false);
	const [companyToAdd, setCompanyToAdd] = useState<Option | null>(null);
	const dispatch = useDispatch<AppDispatch>();
	const handbookCompanies = useSelector(getHandbookCompanies);

	const companyOptions = handbookCompanies?.map((company) => {
		return {
			value: String(company.id),
			label: {
				main: company.name,
				extra: company.innerNumber
			}
		}
	});

	function resetClickHandler(evt: MouseEvent<HTMLButtonElement>) {
		evt.preventDefault();

		setInnerNumber('');
		setCompanyName('');
		setEmployee('');

		dispatch(clearFilters());

		if (closeDialog) {
			closeDialog();
		}
	}

	function viewChangeHandler(evt: SelectChangeEvent) {
		dispatch(setFilters({show: evt.target.value}));

		if (closeDialog) {
			closeDialog();
		}
	}

	function submitClickHandler(evt: MouseEvent<HTMLButtonElement>) {
		evt.preventDefault();

		dispatch(setFilters({
			innerNumber: innerNumber,
			companyName: companyName,
			employeeName: employee
		}));

		if (closeDialog) {
			closeDialog();
		}
	}

	function addCompanyClickHandler(evt: MouseEvent<HTMLButtonElement>) {
		evt.preventDefault();
		dispatch(addCompany(Number(companyToAdd?.value)));
		setIsDialogOpen(false);
		setCompanyToAdd(null);
		toast('Компания успешно добавлена', {type: 'success'});

		if (closeDialog) {
			closeDialog();
		}
	}

	return (
		<StyledScreenControls>
			<VisuallyHidden as="h2">Управление</VisuallyHidden>

			<ControlsForm>
				<CustomTextField
					id="inner-number"
					label="Внутренний номер"
					value={innerNumber}
					width="175px"
					widthSm="100%"
					widthMd="130px"
					marginTopSm="8px"
					onChange={(evt) => {
						setInnerNumber(evt.target.value);
					}}
				/>

				<CustomTextField
					id="company-name"
					label="Название компании"
					value={companyName}
					width="175px"
					widthSm="100%"
					widthMd="130px"
					onChange={(evt) => {
						setCompanyName(evt.target.value);
					}}
				/>

				<CustomTextField
					id="employee"
					label="Сотрудник"
					value={employee}
					width="191px"
					widthSm="100%"
					widthMd="130px"
					onChange={(evt) => {
						setEmployee(evt.target.value);
					}}
				/>

				<IconButton type="submit" onClick={submitClickHandler} variant={ButtonVariant.Main}>
					<svg width="24" height="24" viewBox="0 0 24 24">
						<use xlinkHref="/img/sprite.svg#check-circle"></use>
					</svg>

					<span>Применить</span>
				</IconButton>

				<IconButton type="reset" onClick={resetClickHandler} variant={ButtonVariant.Secondary}>
					<svg width="24" height="24" viewBox="0 0 24 24">
						<use xlinkHref="/img/sprite.svg#cross-circle"></use>
					</svg>

					<span>Сбросить</span>
				</IconButton>
			</ControlsForm>

			<ControlsWrapper>
				<ButtonControl icon={ControlButtonIcon.Add} onClick={() => setIsDialogOpen(true)}>
					Добавить
				</ButtonControl>

				<ButtonControl icon={ControlButtonIcon.Excel} marginLeft="20px">Экспортировать в Excel</ButtonControl>

				<FilterForm>
					<CustomTextField
						id="view-option"
						ariaLabel="Отображение"
						select
						options={showOptions}
						value={filters.show}
						width="202px"
						widthSm="100%"
						onChange={viewChangeHandler}
					/>
				</FilterForm>
			</ControlsWrapper>

			<CustomDialog
				open={isDialogOpen}
				onClose={() => setIsDialogOpen(false)}
				title="Добавить компанию"
			>
				<GreyWrapper as="form">
					{companyOptions?.length ? (
						<>
							<CustomAutocomplete
								id="companies-to-add"
								label="Компании"
								options={companyOptions}
								width="400px"
								widthSm="100%"
								value={companyToAdd}
								onChange={(evt, newValue) => setCompanyToAdd(newValue)}
							/>

							<ControlsFlexWrapper>
								<Button
									type="submit"
									variant={ButtonVariant.Main}
									onClick={addCompanyClickHandler}
									disabled={!companyToAdd}
								>
									Добавить
								</Button>
							</ControlsFlexWrapper>
						</>
					) : (
						<p>Нет доступных компаний для добавления</p>
					)}
				</GreyWrapper>
			</CustomDialog>
		</StyledScreenControls>
	);
}

export default ScreenControlsCompanies;
